import { musicAPI } from "../api"

const SET_MUSIC = 'SET_MUSIC'

let initialState = {
   songs: [],
}

const MUSIC_REDUSER = (state = initialState, action) => {
   switch (action.type) {
      case SET_MUSIC:
         return {
            ...state,
            songs: action.payload
         }
      default:
         return state
   }
}

let setMusic = (music) => ({
   type: SET_MUSIC,
   payload: music,
})

export const setMusicSearchTC = (query) => (dispatch) => {
   
   musicAPI.getSearchMusic(query).then(response => {
      if (response.status === 200) {
         dispatch(setMusic(response.data.data))
      }
      
   })
}

export default MUSIC_REDUSER;